import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    roles: [],
  },
  getters: {

    roles: state => state.roles


  },
  mutations: {

  },
  actions: {


    getRoles({ state,commit }) {

      return new Promise((resolve, reject) => {

        db.collection('roles').get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id;
            items.push(item)

          })
          state.roles = items
          resolve()

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },

    saveRole({ commit }, role) {

      return new Promise((resolve, reject) => {

        const ref = db.collection("roles")

        if (role.id) {
          ref.doc(role.id).set({
            ...role,

          }).then(() => {
            resolve()
          }).catch(e => {
            reject()
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          })

        } else {
          ref.doc().set({
            ...role,

          }).then(() => {
            resolve()
          }).catch(e => {
          
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject()
          })

        }

      })


    },
    removeRole({ commit }, id) {

      return new Promise((resolve, reject) => {


        db.collection("roles").doc(id).delete().then(() => {
          resolve()
        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        });

      })

    },

  }
}