<template>
  <div>
    <v-bottom-navigation>
      <v-btn @click="client={};dialog=true">
        <span>Adicionar Novo Cliente</span>
        <v-icon>mdi-plus-circle-outline</v-icon>
      </v-btn>
    </v-bottom-navigation>
    <v-card>
      <v-card-title>
        <div class="flex-grow-1"></div>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Pesquisar"
          single-line
          solo
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        hide-default-footer
        :loading="loadingTable"
        loading-text="Carregando dados"
        :disable-pagination="true"
        :headers="headers"
        :items="desserts"
        :search="search"
      >
        <template v-slot:item.action="{ item }">
          <v-btn @click="editItem(item)" color="info" fab x-small dark class="mr-1">
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>
          <v-btn @click="deleteItem(item)" color="error" fab x-small dark>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
      <v-pagination
        @input="getClients()"
        v-model="pageTable"
        :total-visible="8"
        :length="pageLength"
      ></v-pagination>
    </v-card>

    <!--Dialog-->
    <v-dialog v-model="dialog" scrollable persistent fullscreen>
      <v-card>
        <v-toolbar max-height="64px" dark color="blue">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Cliente</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-toolbar-items>
            <v-btn :loading="btLoading" dark text @click="save()">Salvar</v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="12" sm="8">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="client.name"
                    label="Nome"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="4">
                  <v-text-field v-model="client.nickname" label="Apelido" filled placeholder=" "></v-text-field>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-text-field
                    :loading="loadingCep"
                    v-model="client.cep"
                    @change="reloadCep($event)"
                    @keyup.enter="reloadCep($event)"
                    label="Cep"
                    filled
                    placeholder=" "
                
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-autocomplete
                    :items="states"
                    label="Estado"
                    v-model="client.state"
                    :return-object="false"
                    item-text="name"
                    item-value="id"
                    filled
                    :rules="[...valRequired]"
                    autocomplete="off"
                  ></v-autocomplete>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-autocomplete
                    :items="cities"
                    label="Cidade"
                    v-model="client.city"
                    :return-object="false"
                    item-text="name"
                    :loading="loadingCities"
                    item-value="id"
                    filled
                    :rules="[...valRequired]"
                    autocomplete="off"
                  ></v-autocomplete>
                </v-col>

                <v-col cols="12" sm="10">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="client.address"
                    label="Endereço"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-text-field
                    v-model="client.address_number"
                    label="Número"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-text-field
                    v-model="client.tel"
                    label="Telefone Principal"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-text-field v-model="client.tel2" label="Telefone 2" filled placeholder=" "></v-text-field>
                </v-col>

                <v-col cols="12" sm="4">
                  <v-text-field v-model="client.tel3" label="Telefone 3" filled placeholder=" "></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-textarea
                    filled
                    rows="2"
                    placeholder=" "
                    v-model="client.obs"
                    label="Observações"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>

      <Snackbars name="dialog" />
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import Snackbars from "@/components/Snackbars.vue";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";

export default {
  name: "clients",
  components: { Snackbars },
  mixins: [Validate],
  data() {
    return {
      /*Para a pginação*/
      pageTable: 1,
      items_per_page: 10,
      /** */
      search: "",
      dialog: false,
      btLoading: false,
      loadingCep: false,
      loadingTable: true,
      valid: false,
      client: {},
      loadingCities: true,
      headers: [
        { text: "Cod.", value: "cod" },
        { text: "Nome", value: "name" },
        { text: "", value: "action", sortable: false }
      ]
    };
  },
  methods: {
    
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;

        this.$store
          .dispatch("saveClient", this.client)
          .then(() => {
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              name: "application"
            });
            this.dialog = false;
            this.$refs.form.reset();
            this.btLoading = false;
            this.getClients();
          })
          .catch(() => {
            this.dialog = false;
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", { ...msgErrorForm, name: "dialog" });
      }
    },
    deleteItem(item) {
      this.$store.commit("showConfirm", {
        title: "Confirmar Exclusão",
        text: "Tem certeza que dessa excluir este cliente?",
        textYes: "Excluir",
        textNot: "Não",
        funcYes: () => {
          let index = this.desserts.indexOf(item);
          this.desserts.splice(index, 1);

          this.$store.commit("showSnack", {
            ...msgRemoveSuccessForm,
            name: "application"
          });
          this.$store.dispatch("removeClient", item.id).then(() => {
            this.getClients();
          });
        }
      });
    },

    editItem(item) {
      this.client = { ...item };

      this.dialog = true;
    },
    getClients() {
      this.$store
        .dispatch("getClients", {
          page: this.pageTable
        })
        .then(() => {
          this.loadingTable = false;
        })
        .catch(() => {
          this.loadingTable = false;
        });
    },

    reloadCep(val) {
      this.loadingCep = true;

      this.$http
        .get(val)
        .then(res => {
          this.loadingCep = false;
          this.client.address = res.data.logradouro + " - " + res.data.bairro;
          this.client.state = res.data.estado
          
        })
        .catch(() => {
          this.loadingCep = false;
        });
    }
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    },

    desserts() {
      return this.$store.getters.clients;
    },
    config() {
      return this.$store.getters.config;
    },
    pageLength() {
      let length = Math.ceil(this.config.clients_count / this.items_per_page);
      return isNaN(length) ? 0 : length;
    },
    states() {
      return this.$store.getters.states;
    },
    cities() {
      return this.$store.getters.cities;
    }
  },

  watch: {
    "client.state": function(val) {
      this.loadingCities = true;

      this.$store.dispatch("getCities", val).then(() => {
       
        this.loadingCities = false;

      });
    }
  },

  mounted() {
    this.getClients();
    this.$store.dispatch("getStates");
  }
};
</script>