<template>
  <v-container>
    <v-form v-model="valid" ref="form">
      <v-row align="center" justify="space-around">
        <v-col cols="12" md="4">
          <v-row justify="space-around">
            <h3>Login</h3>
          </v-row>
          <v-row class="mt-3">
            <v-text-field
              :rules="[...valRequired,...ValEmail]"
              v-model="user.email"
              label="Email"
              outlined
            ></v-text-field>
          </v-row>
          <v-row>
            <v-text-field
              :rules="[...valRequired]"
              type="password"
              v-model="user.password"
              label="Senha"
              outlined
              @keyup.enter="login"
            ></v-text-field>
          </v-row>
          <v-row>
            <v-btn :loading="btLoading" @click="login" color="primary">Login</v-btn>
          </v-row>
          <v-row class="mt-5">
            Você não tem conta?&nbsp;
            <router-link to="/register">Crie uma conta grátis</router-link>
          </v-row>
          <v-row class="mt-1">
            <a @click="dialog=true;user.email2=''">Recuperar Senha</a>
          </v-row>
        </v-col>
      </v-row>
    </v-form>
    <!--Dialog-->
    <v-dialog v-model="dialog" max-width="600px" persistent>
      <v-card>
        <v-card-title>
          <span class="headline">Recuperar Senha</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form2">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    :rules="[...valRequired,...ValEmail]"
                    v-model="user.email2"
                    label="Email"
                    filled
                    placeholder=" "
                    @keyup.enter="save()"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading2" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--End Dialog-->
  </v-container>
</template>



<script>
//

import Validate from "@/mixins/ValidadeAndMask.js";
import { msgErrorForm ,msgSuccessForm} from "@/global";

export default {
  name: "login",
  mixins: [Validate],

  data() {
    return {
      user: {},
      valid: false,
      snackbar: false,
      btLoading: false,
      btLoading2: false,
      dialog: false,
      valid: false
    };
  },

  methods: {
    login: function() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;
        this.$store
          .dispatch("userLogin", this.user)
          .then(() => {
            this.btLoading = false;
          })
          .catch(() => {
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", {
          ...msgErrorForm,
          name: "application"
        });
      }
    },
    save() {
      if (this.$refs.form2.validate()) {
       
        this.btLoading2 = true;
        this.$store
          .dispatch("sendPasswordResetEmail", this.user.email2)
          .then(() => {
        
            this.btLoading2 = false;
            this.dialog = false;
            this.$store.commit("showSnack", {
              ...msgSuccessForm,text:'Confira sua caixa de email',
              name: "application"
            });
          })
          .catch(() => {
            this.btLoading2 = false;
            this.dialog = false;
          });
      } else {
        this.$store.commit("showSnack", {
          ...msgErrorForm,
          name: "application"
        });
      }
    }
  }
};
</script>

