import firebase from "firebase"
import { db } from '@/plugins/firebase.js'
import router from '@/router'
import * as gb from "@/global";
import hex_sha512 from "@/libs/hex_sha512";
export default {

  namespaced: false,
  state: {

    user: null,
    isAuthenticated: false,
    users: [],
    profiles: [

      { id: 1, name: 'Adminstrador' },
      { id: 2, name: 'Atendente' }

    ]
  },
  getters: {

    isAuthenticated(state) {

      return state.user !== null && state.user !== undefined
    },

    users: state => state.users,

    profiles: state => state.profiles,
  },
  mutations: {
    setUser(state, payload) {
      state.user = payload
    },

    setIsAuthenticated(state, payload) {
      state.isAuthenticated = payload
    },
  },
  actions: {


    sendPasswordResetEmail({ commit }, email) {


      return new Promise((resolve, reject) => {

        firebase.auth()
          .sendPasswordResetEmail(email)
          .then(function () {
          
            resolve()
          })
          .catch(function (e) {
            let msg = "Aconteceu algo inesperado: " + e.message
            if (e.code == 'auth/user-not-found') {
              msg = 'Não Localizamos esse registro de email'
            }

            commit('showSnack', { 'text': msg, icon: 'mdi-cancel', color: 'error', name: "application" })
            reject()
          })
      })

    },

    updatePassword({ commit }, { email, password, newPassword }) {


      return new Promise((resolve, reject) => {

        const credentials = firebase.auth.EmailAuthProvider.credential(
          email,
          password,
        );

        const currentUser = firebase.auth().currentUser;

        currentUser.reauthenticateWithCredential(credentials)
          .then(() => {

            currentUser
              .updatePassword(newPassword)
              .then(function () {

                resolve()
              })
              .catch(function (e) {
                let msg = "Aconteceu algo inesperado: " + e.message
                if (e.code == 'auth/weak-password') {
                  msg = 'Senha deve possuir no mínimo 6 caracteres'
                }
                commit('showSnack', { 'text': msg, icon: 'mdi-cancel', color: 'error', name: "application" })


                reject()
              });


          })
          .catch((e) => {


            let msg = "Aconteceu algo inesperado: " + e.message
            if (e.code == 'auth/wrong-password') {
              msg = 'Senha não correspondente'
            }
            else if (e.code == 'auth/too-many-requests') {

              msg = 'Tente novamento mais tarde'
            }

            commit('showSnack', { 'text': msg, icon: 'mdi-cancel', color: 'error', name: "application" })

            reject();

          })
      })
    },

    userLogin({ commit }, { email, password }) {


      return new Promise((resolve, reject) => {

        firebase
          .auth()
          .signInWithEmailAndPassword(email, password)
          .then(user => {
            commit('setUser', user)
            commit('setIsAuthenticated', true)
            router.push('/home')

            resolve()

          })
          .catch((e) => {

            let msg = "Aconteceu algo inesperado: " + e.message
            if (e.code == 'auth/wrong-password' || e.code == 'auth/user-not-found') {
              msg = 'Usuário e senha não correspondente'
            }

            commit('showSnack', { 'text': msg, icon: 'mdi-cancel', color: 'error', name: "application" })

            commit('setUser', null)
            commit('setIsAuthenticated', false)

            reject();

          })
      })
    },

    register({ commit }, user) {

      return new Promise((resolve, reject) => {

        firebase
          .auth()
          .createUserWithEmailAndPassword(user.email, user.password)
          .then(fireUser => {



            db.collection("users").doc(fireUser.user.uid).set({
              ...user, password: hex_sha512(user.password)

            }).then(() => {

            }).catch(e => {
              commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            })

            commit('setUser', user)
            commit('setIsAuthenticated', true)


            router.push('/home')

            resolve();
          })
          .catch((e) => {

            let msg = "Aconteceu algo inesperado: " + e.message
            if (e.code == 'auth/email-already-in-use') {
              msg = 'Este Email já está sendo para outro usuário'
            } else if (e.code == 'auth/weak-password') {
              msg = 'Senha deve possuir no mínimo 6 caracteres'
            }
            commit('showSnack', { 'text': msg, icon: 'mdi-cancel', color: 'error', name: "application" })
            commit('setUser', null)
            commit('setIsAuthenticated', false)

            reject();

          })
      })
    },

    userSignOut({ commit }) {
      firebase
        .auth()
        .signOut()
        .then(() => {
          commit('setUser', null)
          commit('setIsAuthenticated', false)
          router.push('/login')
        })
        .catch(() => {
          commit('setUser', null)
          commit('setIsAuthenticated', false)
          router.push('/login')
        })
    },


    getUsers({ state, commit }) {

      return new Promise((resolve, reject) => {

        db.collection('users').get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id
            items.push(item)

          })

          state.users = items
          resolve()
        }).catch(e => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })
      })


    },

    saveUser({ commit }, user) {

      return new Promise((resolve, reject) => {

        const ref = db.collection("users")

        if (user.id) {
          ref.doc(user.id).set({
            ...user,

          }).then(() => {
            resolve()
          }).catch(e => {
            reject()
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          })

        } else {
          ref.doc().set({
            ...user,

          }).then(() => {
            resolve()
          }).catch(e => {
            reject()
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          })

        }

      })

    },
    removeUser({ commit }, id) {
      return new Promise((resolve, reject) => {

        db.collection("users").doc(id).delete().then(() => {
          resolve()

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },


  }
}