<template>
  <div>
    <v-bottom-navigation>
      <v-btn @click="user={};dialog=true">
        <span>Adicionar Nova Usuário</span>
        <v-icon>mdi-plus-circle-outline</v-icon>
      </v-btn>
    </v-bottom-navigation>
    <v-card>
      <v-card-title>
        <div class="flex-grow-1"></div>

        <v-row>
          <v-col cols="12" sm="6">
            <v-autocomplete
              v-model="filterRoles"
              :items="rolesItens"
              label="Filtrar por Cargo"
              :return-object="true"
              item-text="name"
              item-value="id"
              multiple
              chips
              solo
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="6">
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Pesquisar"
              single-line
              hide-details
              solo
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-title>
      <v-data-table
        :loading="loadingTable"
        loading-text="Carregando dados"
        :headers="headers"
        :items="desserts"
        :search="search"
      >
        <template v-slot:item.action="{ item }">
          <v-btn @click="editItem(item)" color="info" fab x-small dark class="mr-1">
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>
          <v-btn @click="deleteItem(item)" color="error" fab x-small dark>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

    <!--Dialog-->
    <v-dialog
      v-model="dialog"
      scrollable
      persistent
      fullscreen
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar max-height="64px" dark color="blue">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Usuário</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-toolbar-items>
            <v-btn :loading="btLoading" dark text @click="save()">Salvar</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="user.name"
                    label="Nome"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" md="6">
                  <v-autocomplete
                    :items="profileItens"
                    label="Perfil de acesso ao sistema"
                    :return-object="true"
                    item-text="name"
                    v-model="user.profiles"
                    item-value="id"
                    filled
                    :rules="[...valRequired]"
                  ></v-autocomplete>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-autocomplete
                    :items="rolesItens"
                    label="Selecione uma ou mais cargo(s)"
                    :return-object="true"
                    item-text="name"
                    v-model="user.roles"
                    item-value="id"
                    :rules="[...valRequiredForMultiple]"
                    multiple
                    
                    chips
                  ></v-autocomplete>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired,...ValEmail]"
                    v-model="user.email"
                    label="Email"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="user.nickname" label="Apelido" filled placeholder=" "></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="user.tel"
                    label="Telefone Principal"
                    v-mask="maskTel"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="user.tel2"
                    label="Telefone 2"
                    v-mask="maskTel"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    v-model="user.tel3"
                    label="Telefone 3"
                    v-mask="maskTel"
                    filled
                    placeholder=" "
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    filled
                    rows="2"
                    placeholder=" "
                    v-model="user.obs"
                    label="Observações"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>
      <Snackbars name="dialog" />
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import Snackbars from "@/components/Snackbars.vue";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";

export default {
  name: "Users",
  components: { Snackbars },
  mixins: [Validate],
  props: ["loadingTable"],
  data() {
    return {
      search: "",
      dialog: false,
      btLoading: false,
      valid: false,
      filterRoles: [],
      user: {},
      headers: [
        { text: "Nome", value: "name" },
        { text: "Cargo", value: "roles_str" },
        { text: "", value: "action", sortable: false }
      ]
    };
  },
  methods: {
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;
        this.$store
          .dispatch("saveUser", this.user)
          .then(() => {
            this.$refs.form.reset();
            this.dialog = false;

            this.btLoading = false;
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              name: "application"
            });
            this.$store.dispatch("getUsers");
          })
          .catch(() => {
            this.dialog = false;
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", { ...msgErrorForm, name: "dialog" });
      }
    },
    deleteItem(item) {
      this.$store.commit("showConfirm", {
        title: "Confirmar Exclusão",
        text: "Tem certeza que dessa excluir este usuário?",
        textYes: "Excluir",
        textNot: "Não",
        funcYes: () => {
          let index = this.desserts.indexOf(item);
          this.desserts.splice(index, 1);
          this.$store.commit("showSnack", {
            ...msgRemoveSuccessForm,
            name: "application"
          });
          this.$store.dispatch("removeUser", item.id).then(() => {
            this.$store.dispatch("getUsers");
          });
        }
      });
    },

    editItem(item) {
      this.user = { ...item };

      this.dialog = true;
    }
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    },

    desserts() {
      const filterIds = this.filterRoles.map(el => el.id);
     
      let items = [];
      this.$store.getters.users.map(item => {
        let hasItem = false;
       
        const str = item.roles
          .map(el => {
            hasItem = hasItem ? hasItem : filterIds.includes(el.id)
            return el.name;
          })
          .join(", ");
        if (hasItem || filterIds.length==0) {
          items.push({ ...item, roles_str: str });
        }
      });

      return items;
    },
    rolesItens() {
      
      return this.$store.getters.roles;
    },
    profileItens() {
      return this.$store.getters.profiles;
    }
  },
  mounted() {}
};
</script>