import firebase from "firebase"
import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    clients: [],
  },
  getters: {

    clients: state => state.clients


  },
  mutations: {

  },
  actions: {


    getClients({ state,commit }, pagination) {

      return new Promise((resolve, reject) => {

        pagination.limit = 10;
        pagination.offset = (pagination.page * pagination.limit) - pagination.limit

        let func = firebase.functions().httpsCallable('paginateClients')
        func(pagination).then(function (result) {

          state.clients = result.data
          resolve()
        }).catch(() => {

          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado" })
          reject()

        })

      })
    },

    saveClient({ commit }, client) {


      return new Promise((resolve, reject) => {

        const ref = db.collection("clients")

        if (client.id) {
          ref.doc(client.id).set({
            ...client,

          }).then(() => {

            resolve();

          }).catch(e => {

            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })

            reject();
          })

        } else {

          let configRef = db.collection("config").doc("default")

          return db.runTransaction(transaction => {

            return transaction.get(configRef).then(function (configDoc) {
              let seq = configDoc.data().clients_seq + 1
              let count = configDoc.data().clients_count + 1
              transaction.update(configRef, { clients_seq: seq, clients_count: count })
              transaction.set(ref.doc(), { ...client, cod: seq })
            })
          }).then(function () {
            resolve()

          }).catch(function (e) {

            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject()

          });


        }

      })


    },
    removeClient({ commit }, id) {

      return new Promise((resolve, reject) => {

        const ref = db.collection("clients")

        const batch = db.batch()
        const refConfigDefault = db.collection("config").doc('default')
        const decrement = firebase.firestore.FieldValue.increment(-1)

        batch.delete(ref.doc(id))
        batch.set(refConfigDefault, { clients_count: decrement }, { merge: true })

        batch.commit().then(() => {
          resolve()
        }).catch((e) => {

          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })

    },

  }
}