import firebase from "firebase"
import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    orders: [],
    ordersPanel:[]
  },
  getters: {

    orders: state => state.orders,
    ordersPanel: state => state.ordersPanel


  },
  mutations: {

  },
  actions: {



    getOrdersPanel({ state, commit }) {


      return new Promise((resolve, reject) => {


        db.collection('orders').onSnapshot(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id
            items.push(item)

          })
         
          state.ordersPanel = items
          resolve()
        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })
      })
    },

    getOrders({ state, commit }, pagination) {

      return new Promise((resolve, reject) => {

        pagination.limit = 10;
        pagination.offset = (pagination.page * pagination.limit) - pagination.limit

        let func = firebase.functions().httpsCallable('paginateOrders')
        func(pagination).then((result) => {

          state.orders = result.data
          resolve()

        }).catch(() => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado" })
          reject()


        })

      })
    },

    saveOrder({ commit }, order) {


      return new Promise((resolve, reject) => {

        const ref = db.collection("orders")

        if (order.id) {
          ref.doc(order.id).set({
            ...order,

          }).then(() => {

            resolve();

          }).catch(e => {
            reject();
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          })

        } else {

          let configRef = db.collection("config").doc("default")

          return db.runTransaction(transaction => {

            return transaction.get(configRef).then(function (configDoc) {
              let seq = configDoc.data().orders_seq + 1
              let count = configDoc.data().orders_count + 1
              transaction.update(configRef, { orders_seq: seq, orders_count: count })
              transaction.set(ref.doc(), { ...order, cod: seq })
            })
          }).then(function () {
            resolve()

          }).catch(function (e) {

            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject()
          });


        }

      })


    },
    removeOrder({ commit }, id) {


      return new Promise((resolve, reject) => {

        const ref = db.collection("orders")

        const batch = db.batch()
        const refConfigDefault = db.collection("config").doc('default')
        const decrement = firebase.firestore.FieldValue.increment(-1)

        batch.delete(ref.doc(id))
        batch.set(refConfigDefault, { orders_count: decrement }, { merge: true })

        batch.commit().then(() => {
          resolve()
        }).catch((e) => {

          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },

  }
}