<template>
  <v-dialog v-model="show" max-width="290">
    <v-card>
      <v-card-title class="headline">{{title}}</v-card-title>

      <v-card-text>{{text}}</v-card-text>

      <v-card-actions>
        <div class="flex-grow-1"></div>

        <v-btn color=" darken-1" text @click="not()">{{textNot}}</v-btn>

        <v-btn color="info darken-1" text @click="yes()">{{textYes}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "confirm",
  computed: {
    show: {
      get: function() {
        return this.$store.state.confirm.show;
      },

      set: function(newValue) {
        this.$store.state.confirm.show = newValue;
      }
    },
    text() {
      return this.$store.state.confirm.text;
    },
    title() {
      return this.$store.state.confirm.title;
    },
    textYes() {
      return this.$store.state.confirm.textYes;
    },
    textNot() {
      return this.$store.state.confirm.textNot;
    }
  },
  data() {
    return {};
  },
  methods: {
    yes() {
      this.show = false;
      this.$store.state.confirm.funcYes && this.$store.state.confirm.funcYes();
    },
    not() {
      this.show = false;
      this.$store.state.confirm.funcNot && this.$store.state.confirm.funcNot();
    }
  }
};
</script>