import Vue from 'vue'
import Vuex from 'vuex'


import category from './modules/category'
import user from './modules/user'
import product from './modules/product'
import role from './modules/role'
import client from './modules/client'
import order from './modules/order'
import config from './modules/config'
import country from './modules/country'
Vue.use(Vuex)

export default new Vuex.Store({

  modules: { category,user,product,role,client,order,config,country },
  state: {
  
    
    snackbar:{ 

      application :{},
      dialog:{}

    },
    confirm: {}
  },

  getters: {
  
   


  },
  mutations: {
    
    showSnack(state, payload) {
     
      state.snackbar[payload.name] = {...payload,show:true}

      
    },
    showConfirm(state, payload) {
    
      
      state.confirm = {...payload,show:true}

    

    },
    hideSnack(state,payload) {
      
      state.snackbar[payload].show = false
    },

  },
  actions: {

  }
 
})
