<template>
  <div>
    <v-card>
      <v-card-title>
        <div class="flex-grow-1"></div>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Pesquisar"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
      
      
        :headers="headers"
        :items="desserts"
        :search="search"
        :loading="loadingTable"
        loading-text="Carregando dados..."
      >
        <template v-slot:item.action="{ item }">
          <v-btn @click="editItem(item)" color="info" fab x-small dark class="mr-1">
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>
          <v-btn @click="deleteItem(item)" color="error" fab x-small dark>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>

    
    </v-card>

    <!--Dialog-->
    <v-dialog v-model="dialog" scrollable persistent fullscreen>
      <v-card>
        <v-toolbar max-height="64px" dark color="blue">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Pedido</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-toolbar-items>
            <v-btn :loading="btLoading" dark text @click="save()">Salvar</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="12">
                  <v-textarea
                    filled
                    rows="2"
                    placeholder=" "
                    v-model="order.obs"
                    label="Observações"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>

      <Snackbars name="dialog" />
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>




<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import Snackbars from "@/components/Snackbars.vue";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";

export default {
  name: "orders",
  components: { Snackbars },
  mixins: [Validate],

  data() {
    return {
     
      loadingTable: true,
      search: "",
      dialog: false,
      btLoading: false,
      valid: false,
      order: {},
      headers: [
        { text: "Cod.", value: "cod" },
        { text: "Observações", value: "obs" },
        { text: "", value: "action", sortable: false }
      ]
    };
  },
  methods: {
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;

        this.$store
          .dispatch("saveOrder", this.order)
          .then(() => {
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              name: "application"
            });
            this.dialog = false;
            this.$refs.form.reset();
            this.btLoading = false;

           
          })
          .catch(() => {
            this.dialog = false;
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", { ...msgErrorForm, name: "dialog" });
      }
    },
    deleteItem(item) {
      this.$store.commit("showConfirm", {
        title: "Confirmar Exclusão",
        text: "Tem certeza que dessa excluir este pedido?",
        textYes: "Excluir",
        textNot: "Não",
        funcYes: () => {
          let index = this.desserts.indexOf(item);
          this.desserts.splice(index, 1);
          this.$store.commit("showSnack", {
            ...msgRemoveSuccessForm,
            name: "application"
          });
          this.$store.dispatch("removeOrder", item.id).then(() => {
       
          });
        }
      });
    },

    editItem(item) {
      this.order = { ...item };
      this.dialog = true;
    }
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    },

    desserts() {
      return this.$store.getters.ordersPanel;
    },
    
  },
  mounted() {

    this.$store
      .dispatch("getOrdersPanel")
      .then(() => {
        this.loadingTable = false;
      })
      .catch(() => {
        this.loadingTable = false;
      });
   ;
  }
};
</script>