<template>
 
    <v-snackbar v-model="show" :color="color" :timeout="2000">
      <v-icon>{{icon}}</v-icon>
      <div class="ml-3">{{text}}</div>
      <v-btn color="white" text @click="close()">Ok</v-btn>
    </v-snackbar>
 
</template>

<script>
export default {
  name: "snackbars",
  props: ['name'],
  computed: {
    show: {
      get: function() {
       
        return this.$store.state.snackbar[this.name].show;
      },

      set: function(newValue) {
        this.$store.state.snackbar[this.name].show = newValue;
      }
    },
    color() {
      return this.$store.state.snackbar[this.name].color ;
    },
    text() {
      return this.$store.state.snackbar[this.name].text;
    },
    icon() {
      return this.$store.state.snackbar[this.name].icon;
    }
  },
  data() {
    return {
     
    };
  },
  methods: {
    close() {
      
      this.$store.commit("hideSnack",this.name);
    }
  }
};
</script>