import { db } from '@/plugins/firebase.js'
export default {

  namespaced: false,
  state: {

    config: {},
  },
  getters: {

    config: state => state.config


  },
  mutations: {

  },
  actions: {


    getConfig({ state }) {

      db.collection('config').doc('default').onSnapshot(snapshot => {
       
       
        state.config = snapshot.data()

      })
    },

   
  

  }
}