<template>
  <div>
    <div class="pa-5">
      <v-alert prominent type="error">
        <v-row align="center">
          <v-col
            class="grow"
          >Nunc nonummy metus. Nunc interdum lacus sit amet orci. Nullam dictum felis eu pede mollis pretium. Cras id dui.</v-col>
          <v-col class="shrink">
            <v-btn @click="dialog=true">Alterar Senha</v-btn>
          </v-col>
        </v-row>
      </v-alert>
    </div>

    <!--Dialog-->
    <v-dialog v-model="dialog" max-width="600px" persistent>
      <v-card>
        <v-card-title>
          <span class="headline">Alterar Senha</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="user.password"
                    label="Senha Antiga"
                    filled
                    type="password"
                    placeholder=" "
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="user.newPassword"
                    label="Senha Nova"
                    filled
                    type="password"
                    placeholder=" "
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="[...valRequired,...ValPassword2]"
                    v-model="user.newPassword2"
                    label="Repita a Senha"
                    filled
                    type="password"
                    placeholder=" "
                    @keyup.enter="save()"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";
export default {
  name: "settings",
  components: {},
  mixins: [Validate],
  data() {
    return {
      dialog: false,
      user: {},
      valid: false,
      btLoading: false,
      ValPassword2:   v => v===this.user.newPassword || 'Senha diferente da anterior',      
    };
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    }
  },
  methods: {
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;
        this.$store
          .dispatch("userReauthentic", {
            ...this.user,
            email: this.userLogin.email
          })
          .then(() => {
            this.dialog = false;
            this.btLoading = false;
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              text: "Senha alterada com sucesso!",
              name: "application"
            });
            this.$refs.form.reset();
          })
          .catch(() => {
            this.$refs.form.reset();
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", {
          ...msgErrorForm,
          name: "application"
        });
      }
    }
  },
  mounted() {}
};
</script>