<template>
  <v-container>
    <v-form v-model="valid" ref="form">
      <v-row align="center" justify="space-around">
        <v-col cols="12" md="4">
          <v-row justify="space-around">
            <h3>Crie um nova conta</h3>
          </v-row>

          <v-row class="mt-3">
            <v-text-field
              v-model="user.name"
              :rules="[...valRequired]"
              label="Nome Completo"
              outlined
            ></v-text-field>
          </v-row>

          <v-row>
            <v-text-field
              v-mask="maskTel"
              v-model="user.tel"
              :rules="[...valRequired]"
              label="Telefone para contato"
              outlined
            ></v-text-field>
          </v-row>
          <v-row>
            <v-text-field
              v-model="user.email"
              :rules="[...valRequired,...ValEmail]"
              label="Email"
              outlined
            ></v-text-field>
          </v-row>
          <v-row>
            <v-text-field
              @keyup.enter="signUp"
              type="password"
              :rules="[...valRequired]"
              v-model="user.password"
              label="Senha"
              outlined
            ></v-text-field>
          </v-row>
          <v-row>
            <v-text-field
              @keyup.enter="signUp"
              type="password"
              :rules="valTestRepeat"
              v-model="password2"
              label="Repita a senha"
              outlined
            ></v-text-field>
          </v-row>

          <v-row>
            <v-btn :loading="btLoading" @click="signUp" color="primary">Registrar</v-btn>
          </v-row>
          <v-row class="mt-5">
            Ou retorne ao&nbsp;
            <router-link to="/login">login</router-link>
          </v-row>
        </v-col>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js"
import { msgErrorForm } from "@/global"

export default {
  name: "Register",
  mixins: [Validate],
  data() {
    return {
      user: {},
      valid: false,
      password2: "",
      btLoading: false,
      valTestRepeat: [
        v => v == this.user.password || "Senha não correspondente"
      ]
    }
  },
  methods: {
    signUp() {
      if (this.$refs.form.validate()) {
         this.btLoading = true
        this.$store
          .dispatch("register", this.user)
          .then(() => {
            this.btLoading = false
          })
          .catch(() => {
            this.btLoading = false
          })
      } else {
        this.$store.commit("showSnack", {
          ...msgErrorForm,
          name: "application"
        })
      }
    }
  }
}
</script>
