<template>
  <v-card>
    <v-tabs v-model="tab" centered icons-and-text class="tabs-bar-no-radius">
      <v-tabs-slider></v-tabs-slider>

      <v-tab href="#tab-1">
        Usuários
        <v-icon>mdi-account-supervisor</v-icon>
      </v-tab>

      <v-tab href="#tab-2">
        Cargos
        <v-icon>mdi-account-badge-outline</v-icon>
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item :value="'tab-1'">
        <v-card flat>
          <Users :loadingTable="loadingTableUsers" />
        </v-card>
      </v-tab-item>

      <v-tab-item :value="'tab-2'">
        <v-card flat>
          <Roles :loadingTable="loadingTableRoles" />
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
import Users from "@/views/Users";
import Roles from "@/views/Roles";
export default {
 
  components: {
    Users,
    Roles
  },
  data() {
    return {
      tab: null,
      loadingTableUsers: true,
      loadingTableRoles: true
    };
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    }
  },
  mounted() {
    this.$store
      .dispatch("getUsers")
      .then(() => {
        this.loadingTableUsers = false;
      })
      .catch(() => {
        this.loadingTableUsers = false;
      });

    this.$store
      .dispatch("getRoles")
      .then(() => {
        this.loadingTableRoles = false;
      })
      .catch(() => {
        this.loadingTableRoles = false;
      });
  }
};
</script>
