import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    products: [],
  },
  getters: {

    products: state => state.products

  },
  mutations: {



  },
  actions: {

    getProducts({ state,commit }) {

      return new Promise((resolve, reject) => {

        // db.collection('products').orderBy("name").onSnapshot(snapshot => {
        db.collection('products').orderBy("name").get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id
            items.push(item)
            resolve()

          })
          state.products = items

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },

    saveProduct({ commit }, product) {


      return new Promise((resolve, reject) => {

        const ref = db.collection("products")

        if (product.id) {
          ref.doc(product.id).set({
            ...product,

          }).then(() => {

            resolve();

          }).catch(e => {
            
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject();
          })



        } else {
          ref.doc().set({
            ...product,

          }).then(() => {
            resolve();
          }).catch(e => {
            
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject();
          })

        }

      })

    },
    removeProduct({ commit }, id) {
      return new Promise((resolve, reject) => {

        db.collection("products").doc(id).delete().then(() => {
          resolve()
        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },



  }
}