import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    countries: [],
    cities: [],
    states: [],
  },
  getters: {

    countries: state => state.countries,
    states: state => state.states,
    cities: state => state.cities


  },
 
  mutations: {

  },
  actions: {


    getStates({ state,commit }) {

      return new Promise((resolve, reject) => {

        db.collection('countries/BR/states').get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id;
            items.push(item)

          })
          state.states = items
          resolve()

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },
    getCities({ state,commit },st) {

      return new Promise((resolve, reject) => {

        db.collection('countries/BR/states/'+st+'/cities').get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id;
            items.push(item)

          })
          state.cities = items
          resolve()

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })

      })
    },

   
    

  }
}