

export default {
  
  data() {
    return {
     
      valRequired: [
       
        v => !!v || 'Campo é obrigatório',
           
      ],
      valRequiredForMultiple: [
       
        v => (!!v  && !!v[0]) || 'Campo é obrigatório',
           
      ],
      ValMin5: [
       
        v => (v+'').length >= 5 || 'Mínimo de 5 caracteres',       
      ],
      ValEmail:[

        v => /.+@.+\..+/.test(v) || 'E-mail não é válido',
      ],
      maskTel : '(##)#########',
      money: {
        decimal: ',',
        thousands: '.',         
        precision: 2,
        masked: false 
      }
     
     
    };
  },
  methods: {

    
  }
};