<template>
  <div>
    <v-bottom-navigation>
      <v-btn @click="category={};dialog=true">
        <span>Adicionar Nova Categoria</span>
        <v-icon>mdi-plus-circle-outline</v-icon>
      </v-btn>
    </v-bottom-navigation>
    <v-card>
      <v-card-title>
        <div class="flex-grow-1"></div>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Pesquisar"
          single-line
          hide-details
          solo
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :loading="loadingTable"
        loading-text="Carregando dados"
        :headers="headers"
        :items="desserts"
        :search="search"
      >
        <template v-slot:item.action="{ item }">
          <v-btn @click="editItem(item)" color="info" fab x-small dark class="mr-1">
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>
          <v-btn @click="deleteItem(item)" color="error" fab x-small dark>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

    <!--Dialog-->
    <v-dialog v-model="dialog" max-width="600px" persistent>
      <v-card>
        <v-card-title>
          <span class="headline">Categoria</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="category.name"
                    label="Nome"
                    filled
                    placeholder=" "
                    @keyup.enter="save()"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";

export default {
  name: "categories",
  components: {},
  props: ["loadingTable"],
  mixins: [Validate],
  data() {
    return {
      search: "",
      dialog: false,
      btLoading: false,
      valid: false,
      category: {},
      headers: [
        { text: "Nome", value: "name" },
        { text: "", value: "action", sortable: false }
      ]
    };
  },
  methods: {
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;

        this.$store
          .dispatch("saveCategory", this.category)
          .then(() => {
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              name: "application"
            });
            this.dialog = false;
            this.$refs.form.reset();
            this.btLoading = false;
            this.$store.dispatch("getCategories");
          })
          .catch(() => {
            this.dialog = false;
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", {
          ...msgErrorForm,
          name: "application"
        });
      }
    },
    deleteItem(item) {
      this.$store.commit("showConfirm", {
        title: "Confirmar Exclusão",
        text: "Tem certeza que dessa excluir esta categoria?",
        textYes: "Excluir",
        textNot: "Não",
        funcYes: () => {
          let index = this.desserts.indexOf(item);
          this.desserts.splice(index, 1);
          this.$store.commit("showSnack", {
            ...msgRemoveSuccessForm,
            name: "application"
          });
          this.$store.dispatch("removeCategory", item.id).then(() => {
            this.$store.dispatch("getCategories");
          });
        }
      });
    },

    editItem(item) {
      this.category = { ...item };

      this.dialog = true;
    }
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    },

    desserts() {
      return this.$store.getters.categories;
    }
  },
  mounted() {}
};
</script>