import { db } from '@/plugins/firebase.js'
import * as gb from "@/global";
export default {

  namespaced: false,
  state: {

    categories: [],
  },
  getters: {

    categories: state => state.categories


  },
  mutations: {

  },
  actions: {


    getCategories({ state,commit }) {


      return new Promise((resolve, reject) => {

        // db.collection('categories').onSnapshot(snapshot => {
        db.collection('categories').orderBy("name").get().then(snapshot => {
          const items = []
          snapshot.forEach(doc => {
            let item = doc.data()
            item.id = doc.id
            items.push(item)

          })
          state.categories = items
          resolve()
        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })
      })
    },

    saveCategory({ commit }, category) {

      return new Promise((resolve, reject) => {

        const ref = db.collection("categories")

        if (category.id) {
          ref.doc(category.id).set({
            ...category,

          }).then(() => {
            resolve();
          }).catch(e => {
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject();
           
          })

        } else {
          ref.doc().set({
            ...category,

          }).then(() => {
            resolve();
          }).catch(e => {
            commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
            reject();
           
          })

        }

      })


    },
    removeCategory({ commit }, id) {
      return new Promise((resolve, reject) => {

        db.collection("categories").doc(id).delete().then(() => {
          resolve()

        }).catch((e) => {
          commit('showSnack', { ...gb.msgErrorUnexpected, 'text': "Aconteceu algo inesperado: " + e })
          reject()
        })
      })

    },

  }
}