<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-if="isAuthenticated"
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
    >
      <v-list dense>
        <template v-for="item in items">
          <v-row v-if="item.heading" :key="item.heading" align="center">
            <v-col cols="6">
              <v-subheader v-if="item.heading">{{ item.heading }}</v-subheader>
            </v-col>
            <v-col cols="6" class="text-center">
              <a href="#!" class="body-2 black--text">EDIT</a>
            </v-col>
          </v-row>
          <v-list-group
            v-else-if="item.children"
            :key="item.text"
            v-model="item.model"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon
          >
            <template v-slot:activator>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>{{ item.text }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </template>
            <v-list-item v-for="(child, i) in item.children" :to="child.link" :key="i">
              <v-list-item-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>{{ child.text }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
          <v-list-item v-else :key="item.text" :to="item.link" @click="call(item.click)">
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar :clipped-left="$vuetify.breakpoint.lgAndUp" app color=" ">
      <v-toolbar-title style="width: 300px" class="ml-0 pl-4">
        <v-app-bar-nav-icon v-if="isAuthenticated" @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <span class="hidden-sm-and-down">Comet</span>
      </v-toolbar-title>

      <div class="flex-grow-1"></div>
      <v-btn icon>
        <v-icon>mdi-apps</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon>mdi-bell</v-icon>
      </v-btn>
      <v-btn icon large>
        <v-avatar size="32px" item>
          <v-img src="https://cdn.vuetifyjs.com/images/logos/logo.svg" alt="Vuetify"></v-img>
        </v-avatar>
      </v-btn>
    </v-app-bar>
    <v-content>
      <v-container style="padding:0">
        <router-view></router-view>
        <Snackbars name="application" />
        <Confirm />
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
//import seeding from "@/libs/seeding.js"
import firebase from "firebase";
import Snackbars from "@/components/Snackbars.vue";
import Confirm from "@/components/Confirm.vue";
export default {
  components: { Snackbars, Confirm },
  computed: {},
  props: {
    source: String
  },
  methods: {
    call: function(func = false) {
      if (func) {
        this[func]();
      }
    },

    signOut() {
      this.$store.commit("showConfirm", {
        title: "Confirmar Saída",
        text: "Tem certeza que dessa sair?",
        textYes: "Sair",
        textNot: "Não",
        funcYes: () => {
          this.$store.dispatch("userSignOut");
        }
      });
    }
  },
  data: () => ({
    isAuthenticated: false,
    drawer: null,

    items: [
      { icon: "mdi-home", text: "Home", link: "/home" },
      { icon: "mdi-remote-desktop", text: "Painel de Pedidos", link: "/panel" },
      { icon: "mdi-file-document-box-check", text: "Pedidos", link: "/orders" },
      { icon: "mdi-food", text: "Monte seu cardápio", link: "/carte" },

      { icon: "mdi-account-multiple", text: "Clientes", link: "/clients" },
      {
        icon: "mdi-folder-key-network-outline",
        text: "Usuários",
        link: "/access"
      },
      {
        icon: "mdi-settings-outline",
        text: "Configurações",
        link: "/settings"
      },
      { icon: "mdi-exit-to-app", text: "Sair", click: "signOut" }
    ]
  }),
  mounted() {
    firebase.auth().onAuthStateChanged(user => {
      this.$store.commit("setUser", user);
      this.isAuthenticated = user !== null;
     
      
    });

    this.$store.dispatch("getConfig");

    // seeding()
  }
};
</script>
