<template>
  <div>
    <v-bottom-navigation>
      <v-btn @click="product={};dialog=true;">
        <span>Adicionar Novo Produto</span>
        <v-icon>mdi-plus-circle-outline</v-icon>
      </v-btn>
    </v-bottom-navigation>
    <v-card>
      <v-card-title>
        <div class="flex-grow-1"></div>

        <v-row>
          <v-col cols="12" sm="6">
            <v-autocomplete
              v-model="filterCategories"
              :items="categoriesItens"
              label="Filtrar por Categoria"
              :return-object="true"
              item-text="name"
              item-value="id"
              multiple
              chips
              solo
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" sm="6">
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Pesquisar"
              single-line
              hide-details
              solo
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-title>

      <v-data-table
        :loading="loadingTable"
        loading-text="Carregando dados"
        :headers="headers"
        :items="desserts"
        :search="search"
      >
        <template v-slot:item.action="{ item }">
          <v-btn @click="editItem(item)" color="info" fab x-small dark class="mr-1">
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>
          <v-btn @click="deleteItem(item)" color="error" fab x-small dark>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

    <!--Dialog-->
    <v-dialog
      persistent
      scrollable
      v-model="dialog"
      fullscreen
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar max-height="64px" dark color="blue">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Produto</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-toolbar-items>
            <v-btn :loading="btLoading" dark text @click="save()">Salvar</v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <v-card-text>
          <v-container>
            <v-form v-on:submit.prevent v-model="valid" ref="form">
              <v-row>
                <v-col cols="8">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.name"
                    label="Nome"
                    filled
                    item-value="key"
                    placeholder="Nome "
                  ></v-text-field>
                </v-col>

                <v-col cols="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price"
                    label="Preço principal"
                    v-money="money"
                    filled
                    item-value="key"
                    placeholder=" "
                  ></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-autocomplete
                    :items="categoriesItens"
                    :rules="[...valRequiredForMultiple]"
                    label="Selecione uma ou mais categoria(s)"
                    :return-object="true"
                    item-text="name"
                    item-value="id"
                    v-model="product.categories"
                    multiple
                    filled
                    chips
                  ></v-autocomplete>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    filled
                    rows="2"
                    placeholder=" "
                    v-model="product.description"
                    label="Descrição"
                  ></v-textarea>
                </v-col>

                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model.number="product.numbers_prices"
                    :items="[0,1,2,3,4,5]"
                    label="Quantas opções de preços promocionais"
                    filled
                  ></v-select>
                </v-col>

                <v-col v-if="product.numbers_prices>=1" cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price1"
                    placeholder=" "
                    label="Preço Promocional tabela 1 "
                    v-money="money"
                    prefix="R$"
                    filled
                  ></v-text-field>
                </v-col>

                <v-col v-if="product.numbers_prices>=2" cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price2"
                    placeholder=" "
                    label="Preço Promocional tabela 2 "
                    v-money="money"
                    prefix="R$"
                    filled
                  ></v-text-field>
                </v-col>

                <v-col v-if="product.numbers_prices>=3" cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price3"
                    placeholder=" "
                    label="Preço Promocional tabela 3 "
                    v-money="money"
                    prefix="R$"
                    filled
                  ></v-text-field>
                </v-col>

                <v-col v-if="product.numbers_prices>=4" cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price4"
                    placeholder=" "
                    label="Preço Promocional tabela 4 "
                    v-money="money"
                    prefix="R$"
                    filled
                  ></v-text-field>
                </v-col>

                <v-col v-if="product.numbers_prices>=5" cols="12" sm="6" md="4">
                  <v-text-field
                    :rules="[...valRequired]"
                    v-model="product.price5"
                    placeholder=" "
                    label="Preço Promocional tabela 5 "
                    v-money="money"
                    prefix="R$"
                    filled
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="red" text @click="dialog = false">Cancelar</v-btn>
          <v-btn :loading="btLoading" color="blue darken-1" text @click="save()">Salvar</v-btn>
        </v-card-actions>
      </v-card>

      <Snackbars name="dialog" />
    </v-dialog>
    <!--End Dialog-->
  </div>
</template>

<script>
import Validate from "@/mixins/ValidadeAndMask.js";
import Snackbars from "@/components/Snackbars.vue";
import { msgErrorForm, msgSuccessForm, msgRemoveSuccessForm } from "@/global";

export default {
  name: "products",
  components: { Snackbars },
  mixins: [Validate],
  watch: {},
  props: ["loadingTable"],
  data() {
    return {
      search: "",
      dialog: false,
      btLoading: false,
      valid: false,
      filterCategories: [],
      product: {},
      headers: [
        { text: "Nome", value: "name" },
        { text: "Categoria", value: "categories_str" },
        { text: "", value: "action", sortable: false }
      ]
    };
  },
  methods: {
    open() {},
    save() {
      if (this.$refs.form.validate()) {
        this.btLoading = true;
        this.$store
          .dispatch("saveProduct", this.product)
          .then(() => {
            this.$refs.form.reset();
            this.dialog = false;

            this.btLoading = false;
            this.$store.commit("showSnack", {
              ...msgSuccessForm,
              name: "application"
            });
            this.$store.dispatch("getProducts");
          })
          .catch(() => {
            this.dialog = false;
            this.btLoading = false;
          });
      } else {
        this.$store.commit("showSnack", { ...msgErrorForm, name: "dialog" });
      }
    },
    deleteItem(item) {
      this.$store.commit("showConfirm", {
        title: "Confirmar Exclusão",
        text: "Tem certeza que dessa excluir este produto?",
        textYes: "Excluir",
        textNot: "Não",
        funcYes: () => {
          let index = this.desserts.indexOf(item);
          this.desserts.splice(index, 1);
          this.$store.commit("showSnack", {
            ...msgRemoveSuccessForm,
            name: "application"
          });
          this.$store.dispatch("removeProduct", item.id).then(() => {
            this.$store.dispatch("getProducts");
          });
        }
      });
    },

    editItem(item) {
      this.product = { ...item };
      this.dialog = true;
    }
  },
  computed: {
    userLogin() {
      return this.$store.state.user.user;
    },

    desserts() {
      const filterIds = this.filterCategories.map(el => el.id);
    
      let items = [];
      this.$store.getters.products.map(item => {
        let hasItem = false;
       
        const str = item.categories
          .map(el => {
            hasItem = hasItem ? hasItem : filterIds.includes(el.id)
            return el.name;
          })
          .join(", ");
        if (hasItem || filterIds.length==0) {
          items.push({ ...item, categories_str: str });
        }
      });

      return items;
    },

    categoriesItens() {
      return this.$store.getters.categories;
    }
  }
};
</script>